#works {
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 60rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    text-align: center;
    padding: 0 1rem;
}
.lan {
    color: yellow;
}
.uni {
    color: darkorange;
}

.edu {
    color: yellow;
}
.worksTitle {
    margin: 1rem 0;
    font-size: 3rem;
}
.workDesc {
    font-weight: 300;
    font-size: 1rem;
}
.worksImgs {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100vw;
    max-width: 65rem;
}
.worksImg {
    object-fit:cover;
    height: 20rem;
    margin: 0.5rem;

}
.tab-titles {
    display: flex;
    margin: 20px 0 40px;

}
.workBtn {
    padding: 0.5rem 2.5rem;
    background: white;
    border: none;
    border-radius: 2rem;
    font-size: 1rem;
    margin: 3rem 0;
}
@media screen and (max-width:585px) {
    .workImg {
        height: 48vw;
    }
}
